import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import BasicInfos from './BasicInfos';
import Assessment from './Assessment';
import TableSub from './TableSub';
import { Col, Row, Button, Drawer } from 'antd';
import SuperFetch from '../../../helpers/superFetch';
import UpdateForm from '../../Register';
import HeaderItem from '../../HeaderItem';
import basicStyle from '../../../settings/basicStyle';
import { constant } from '../../../settings';
function EvaluateDetails(props) {
  // Declare a new state variable, which we'll call "count"
  const [claim, setClaim] = useState(null);
  const [patientClaim, setPatientClaim] = useState([]);
  const [policyClaim, setPolicyClaim] = useState([]);
  const [claimIDurl] = useState(props.match.params.id);
  const [drawerVisibale, setDrawerVisibale] = useState(false);
  const { rowStyle } = basicStyle;

  const showDrawerUpdate = () => {
    setDrawerVisibale(true);
  };
  const onCloseUpdate = () => {
    setDrawerVisibale(false);
  };

  useEffect(() => {
    const fetchData = async () => {
      if (claimIDurl) {
        var claimResponse = await SuperFetch.get('claim/claim', {
          id: claimIDurl,
        });
        setClaim(claimResponse.data);
        var policyClaimResponse = await SuperFetch.get(
          'evaluate/policyClaims',
          {
            id: claimResponse.data.id,
          }
        );
        setPolicyClaim(
          policyClaimResponse.data ? policyClaimResponse.data : []
        );
        var patientResponse = await SuperFetch.get('evaluate/patientClaims', {
          id: claimResponse.data.id,
        });
        setPatientClaim(patientResponse.data ? patientResponse.data : []);
      } else {
        setClaim(null);
      }
    };
    fetchData();
  }, [claimIDurl]);

  const handleSave = bill => {
    const fetchData = async () => {
      await SuperFetch.put('claim/billingInfo', bill);
      if (claimIDurl) {
        var claimResponse = await SuperFetch.get('claim/claim', {
          id: claimIDurl,
        });
        setClaim(claimResponse.data);
      } else {
        setClaim(null);
      }
    };
    fetchData();
  };
  return (
    <>
      <Col span={22} offset={1}>
        <HeaderItem title={'Evaluate'} claim={claim} />
        <Row type="flex" style={rowStyle}>
          <Button type="primary" onClick={showDrawerUpdate}>
            Update Claim
          </Button>
          <Drawer
            title="UPDATE CLAIM"
            placement="right"
            closable={false}
            onClose={onCloseUpdate}
            visible={drawerVisibale}
            width={'80%'}
            style={{ background: '#123b78' }}
          >
            <UpdateForm history={props.history} match={props.match} />
          </Drawer>
        </Row>
        <Row style={rowStyle}>
          <BasicInfos
            claim={claim}
            patientClaim={patientClaim}
            policyClaim={policyClaim}
          />
        </Row>
        <Row>
          <Assessment
            claim={claim}
            page={'Evaluate Claim'}
            params={props.match.params}
            history={props.history}
            match={props.match}
            //flag={false}
          />
        </Row>
        <Row>
          {claim && claim.ClaimInfoVisits
            ? claim.ClaimInfoVisits.map(visit => {
                return (
                  <TableSub
                    key={visit.id}
                    visit={visit}
                    claim={claim}
                    patientClaim={patientClaim}
                    handleSave={handleSave}
                  />
                );
              })
            : null}
        </Row>
        <Row type="flex" justify="space-between">
          <Col>
            <span>Total Benefits Usage: </span>
            <span>
              {patientClaim
                .reduce((claimAcc, claimCurr) => {
                  return (
                    claimAcc +
                    claimCurr.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
                      return (
                        visitAcc +
                        visitCurr.BillingInfos.reduce(
                          (billingAcc, billingCurr) => {
                            return billingAcc + billingCurr.approved;
                          },
                          0
                        )
                      );
                    }, 0)
                  );
                }, 0)
                .toFixed(constant.fixedDigital)}
            </span>
          </Col>
          <Col>
            <span>
              Claimed {claim && claim.policyType === 'TLS' ? 'THB' : 'USD'}:{' '}
            </span>
            <span>
              {(claim
                ? claim.policyType === 'TLS'
                  ? claim.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
                      return (
                        visitAcc +
                        visitCurr.BillingInfos.reduce(
                          (billingAcc, BillingCurr) => {
                            return billingAcc + BillingCurr.value;
                          },
                          0
                        )
                      );
                    }, 0) * claim.RCExchangeRate
                  : claim.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
                      return (
                        visitAcc +
                        visitCurr.BillingInfos.reduce(
                          (billingAcc, BillingCurr) => {
                            return billingAcc + BillingCurr.value;
                          },
                          0
                        )
                      );
                    }, 0) * claim.RCExchangeRate
                : 0
              ).toFixed(constant.fixedDigital)}{' '}
              {claim ? claim.reimbusementCurrency : null}
            </span>
          </Col>
          <Col>
            <span>
              Approved {claim && claim.policyType === 'TLS' ? 'THB' : 'USD'}:{' '}
            </span>
            <span>
              {(claim
                ? claim.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
                    return (
                      visitAcc +
                      visitCurr.BillingInfos.reduce(
                        (billingAcc, BillingCurr) => {
                          return billingAcc + BillingCurr.approved;
                        },
                        0
                      )
                    );
                  }, 0)
                : 0
              ).toFixed(constant.fixedDigital)}
            </span>
          </Col>
          <Col>
            <span>Approved R/C: </span>
            <span>
              {(claim
                ? claim.policyType === 'TLS'
                  ? claim.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
                      return (
                        visitAcc +
                        visitCurr.BillingInfos.reduce(
                          (billingAcc, BillingCurr) => {
                            return (
                              billingAcc +
                              BillingCurr.approved / visitCurr.billingRate
                            );
                          },
                          0
                        )
                      );
                    }, 0) * claim.RCExchangeRate
                  : claim.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
                      return (
                        visitAcc +
                        visitCurr.BillingInfos.reduce(
                          (billingAcc, BillingCurr) => {
                            return billingAcc + BillingCurr.approved;
                          },
                          0
                        )
                      );
                    }, 0) * claim.RCExchangeRate
                : 0
              ).toFixed(constant.fixedDigital)}{' '}
              {claim ? claim.reimbusementCurrency : null}
            </span>
          </Col>
        </Row>
      </Col>
    </>
  );
}

export default connect(
  state => ({ idToken: state.Auth.idToken }),
  {}
)(EvaluateDetails);
