import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Form, Switch, Modal, Button, Row, Col, Input, Typography } from 'antd';
import BankDocs from './BankDocs';
import SuperFetch from '../../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import jwtDecode from 'jwt-decode';
import BankInfoStyleWrapper from './bankInfo.style';
import { constant } from '../../../settings';

function Assessment(props) {
  // Declare a new state variable, which we'll call "count"
  const [disabled] = useState(false);
  const [bankDetailsVisible, setBankDetailsVisible] = useState(false);

  const [accessment, setAssessment] = useState({});
  const [buttonInfo, setButtonInfo] = useState({});

  const { getFieldDecorator } = props.form;
  const handleSubmit = e => {
    e.preventDefault();

    props.form.validateFields((err, values) => {
      if (!err) {
        let comment = {
          claimInfoId: props.claim.id,
          group: 'evaluate',
          message: values.rationale,
          createdBy: jwtDecode(props.idToken).id,
        };
        let assessmentData = {
          claimInfoId: props.claim.id,
          ...accessment,
        };
        SuperFetch.post('evaluate/assessment', {
          assessment: assessmentData,
          comment: comment,
          status: buttonInfo.status,
        })
          .then(response => {
            if (response.status === 200) {
              props.history.goBack();
            } else {
              notification('error', 'Something wrong in retrieving claim');
              return false;
            }
          })
          .catch(e => console.log(e));
      }
    });
  };
  const onChange = (e, a) => {
    let newAccessment = { ...accessment, [a.currentTarget.id]: e };
    setAssessment(newAccessment);
  };

  useEffect(() => {
    if (props.claim) {
      const evaluate = (noDeny, reasonableResult, consideration, patial) => {
        if (!noDeny)
          return {
            buttonText: 'DECLINE',
            buttonColor: '#DB2828',
            status: 'pd-de',
          };
        if (!reasonableResult)
          return {
            buttonText: 'CONSIDERATION',
            buttonColor: '#DB2828',
            status: 'pd-co',
          };
        if (consideration)
          return {
            buttonText: 'CONSIDERATION',
            buttonColor: '#DB2828',
            status: 'pd-co',
          };
        if (patial)
          return {
            buttonText: 'PARTIALLY PAY',
            buttonColor: '#EEA220',
            status: 'pd-pa',
          };
        return {
          buttonText: 'APPROVE',
          buttonColor: '#1BA12C',
          status: 'pd-ap',
        };
      };
      let noDeny = props.claim.ClaimInfoVisits.some(visit =>
        visit.BillingInfos.some(bill => bill.approved > 0)
      );
      let reasonableResult =
        !accessment.forPreExisting &&
        !accessment.relatePreExisting &&
        !accessment.otherExclusion &&
        accessment.reasonable &&
        accessment.adhere;
      let consideration = props.claim.ClaimInfoVisits.some(visit =>
        visit.BillingInfos.some(bill => {
          let approved = bill.approved
            ? bill.approved.toFixed(constant.fixedDigital)
            : 0;
          let value = bill.value.toFixed(constant.fixedDigital);
          return Number(approved) > Number(value);
        })
      );
      let patial = props.claim.ClaimInfoVisits.some(visit =>
        visit.BillingInfos.some(bill => {
          let approved = bill.approved
            ? bill.approved.toFixed(constant.fixedDigital)
            : 0;
          let value = bill.value.toFixed(constant.fixedDigital);
          return Number(approved) < Number(value);
        })
      );
      setButtonInfo(evaluate(noDeny, reasonableResult, consideration, patial));
    }
  }, [props.claim, accessment]);

  return (
    <Form layout="inline" onSubmit={handleSubmit} colon={false}>
      <Col xs={24} sm={20} md={20} lg={20} xl={20} xxl={12}>
        <Row>
          <Typography.Title level={4}>This Policy:</Typography.Title>
        </Row>
        <Col span={10}>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span>Is this claim for a Pre-Existing condition?</span>
            </Col>
            <Col span={8}>
              <Switch
                id={'forPreExisting'}
                disabled={disabled}
                onChange={onChange}
              />
            </Col>
          </Row>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span>Does it realte to a Pre-Exsting condition?</span>
            </Col>
            <Col span={8}>
              <Switch
                id={'relatePreExisting'}
                disabled={disabled}
                onChange={onChange}
              />
            </Col>
          </Row>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span>Do any other Policy Exclusions apply?</span>
            </Col>
            <Col span={8}>
              <Switch
                id={'otherExclusion'}
                disabled={disabled}
                onChange={onChange}
              />
            </Col>
          </Row>
        </Col>
        <Col span={12} offset={2}>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span style={{ display: 'block' }}>
                Are all the charges reasonable and customary?
              </span>
            </Col>
            <Col span={8}>
              <Switch
                id={'reasonable'}
                disabled={disabled}
                onChange={onChange}
              />
            </Col>
          </Row>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span style={{ display: 'block' }}>
                Does this claim adhere to the Policy Workding?
              </span>
            </Col>
            <Col span={8}>
              <Switch id={'adhere'} disabled={disabled} onChange={onChange} />
            </Col>
          </Row>
        </Col>
      </Col>
      <Col xs={24} sm={4} md={4} lg={4} xl={4} xxl={4}>
        <Row>
          <Typography.Title level={4}>Documents Bank Details:</Typography.Title>
        </Row>
        <Button type="primary" onClick={() => setBankDetailsVisible(true)}>
          FILE
        </Button>
        <BankInfoStyleWrapper>
          <Modal
            width={'70%'}
            visible={bankDetailsVisible}
            onOk={() => setBankDetailsVisible(false)}
            onCancel={() => setBankDetailsVisible(false)}
            footer={[
              <Button
                key={1}
                type="primary"
                onClick={() => setBankDetailsVisible(false)}
              >
                OK
              </Button>,
            ]}
          >
            <BankDocs key={1} claim={props.claim} />
          </Modal>
        </BankInfoStyleWrapper>
      </Col>
      <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={8}>
        <Col span={16}>
          <Form.Item style={{ width: '100%' }}>
            {getFieldDecorator('rationale', {
              rules: [
                {
                  required: true,
                  message: 'Please input your decision',
                },
              ],
            })(
              <Input.TextArea
                placeholder="Reason for decision..."
                cols={100}
                autoSize={{ minRows: 4 }}
              />
            )}
          </Form.Item>
        </Col>
        <Col span={6} offset={2}>
          <Form.Item>
            <Button
              style={{ background: buttonInfo.buttonColor }}
              htmlType="submit"
            >
              {buttonInfo.buttonText}
            </Button>
          </Form.Item>
        </Col>
      </Col>
    </Form>
  );
}

export default connect(
  state => ({ idToken: state.Auth.idToken }),
  {}
)(Form.create()(Assessment));
