import React from 'react';
import { Row, Col, Typography, Table } from 'antd';
import Information from '../../Information';
import moment from 'moment';
import { sum } from 'd3-array';
import './global.css';
import { WidgetWrapper } from './style';
import { constant } from '../../../settings';
export default function BasicInfos(props) {
  let policyApprovedYTD;
  let patientApprovedYTD;
  if (props.policyClaim)
    policyApprovedYTD = props.policyClaim
      .reduce((acc, curr) => {
        return (
          acc +
          curr.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
            return (
              visitAcc +
              visitCurr.BillingInfos.reduce((billingAcc, billingCurr) => {
                return billingAcc + billingCurr.approved;
              }, 0)
            );
          }, 0)
        );
      }, 0)
      .toFixed(constant.fixedDigital);
  if (props.patientClaim)
    patientApprovedYTD = props.patientClaim
      .reduce((acc, curr) => {
        return (
          acc +
          curr.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
            return (
              visitAcc +
              visitCurr.BillingInfos.reduce((billingAcc, billingCurr) => {
                return billingAcc + billingCurr.approved;
              }, 0)
            );
          }, 0)
        );
      }, 0)
      .toFixed(constant.fixedDigital);
  const columns = [
    {
      title: 'Date',
      dataIndex: 'createdAt',
      key: 'createdAt',
      render: (text, record) => {
        return moment(text).format('DD MMM YYYY');
      },
    },
    {
      title: 'UCN',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: 'Cause',
      dataIndex: 'cause',
      key: 'cause',
      ellipsis: true,
    },
    {
      title: 'Approved Amount',
      render: (text, record) => {
        return sum(record.ClaimInfoVisits, visit =>
          sum(visit.BillingInfos, billing => billing.value)
        ).toFixed(2);
      },
    },
  ];
  const rowStyle = {
    width: '100%',
    display: 'flex',
    flexFlow: 'row wrap',
  };
  const colStyle = {
    marginBottom: '26px',
    border: 'solid white',
    borderWidth: '0.07ex 0.07ex 0.07ex 0.07ex',
    borderStyle: 'outset',
  };
  const gutter = 16;
  return props.claim ? (
    <Row style={rowStyle} gutter={gutter}>
      <Col xs={24} sm={24} md={12} lg={8} xl={6} xxl={4} style={colStyle}>
        <Row>
          <Typography.Title level={4}>This Policy:</Typography.Title>
        </Row>
        <Row>
          <Col span={12}>
            <Information title="Approved YTD" content={policyApprovedYTD} />
            <Information title="Policy Type" content={props.claim.policyType} />
            <Information title="Insured" content="?" />
            <Information title="Previous" content="?" />
          </Col>
          <Col span={12}>
            <Information
              title="Gross Premium"
              content={props.claim.policyType}
            />
            <Information title="Start Date" content="?" />
            <Information title="Payment Frequency" content="?" />
          </Col>
        </Row>
      </Col>
      <Col xs={24} sm={24} md={12} lg={10} xl={10} xxl={6} style={colStyle}>
        <Col span={12}>
          <Typography.Title level={4}>This Claim:</Typography.Title>
          <Information
            title="Registered"
            content={moment(props.claim.createdAt).format('DD MMM YYYY')}
          />
          <Information title="Cause" content={props.claim.cause} />
          <Information
            title="claimed amount"
            content={
              (props.claim.ClaimInfoVisits
                ? sum(props.claim.ClaimInfoVisits, visit =>
                    sum(visit.BillingInfos, billing => billing.value)
                  ).toFixed(2)
                : 'unknown') + ' USD'
            }
          />
        </Col>
        <Col span={12}>
          <Information
            title="VIP"
            content={props.claim.policyVip ? 'Yes' : 'No'}
          />
          <Information
            title="Group"
            content={props.claim.policyGroupPolicy ? 'Yes' : 'No'}
          />
          <Information title="GOP" content={props.claim.gop ? 'Yes' : 'No'} />
          <Information
            title="Fraud"
            content={props.claim.fraud ? 'Yes' : 'No'}
          />
        </Col>
      </Col>
      <Col xs={24} sm={24} md={12} lg={6} xl={8} xxl={4} style={colStyle}>
        <Row>
          <Typography.Title level={4}>This Patient:</Typography.Title>
        </Row>
        <Information title="Approved YTD" content={patientApprovedYTD} />
        <Information title="Age" content={props.claim.age} />
        <Information
          title="Consideration YTD"
          content={props.claim.considerationYTD}
        />
        <Information title="Gender" content={props.claim.gender} />
      </Col>
      <Col xs={24} sm={24} md={12} lg={24} xl={24} xxl={10} style={colStyle}>
        <Row>
          <Typography.Title level={4}>
            Patient Previous Claims:
          </Typography.Title>
        </Row>
        <WidgetWrapper>
          <Table
            columns={columns}
            dataSource={props.patientClaim}
            rowKey="id"
            size="small"
            pagination={false}
            scroll={{ y: 110 }}
          />
        </WidgetWrapper>
      </Col>
    </Row>
  ) : null;
}
